.btn {
    height: 50px;
    background-color: #2196F3;
    color: white;
    border: none;
    border-radius: 4px;
    width: 100%;
    max-width: 502px;
    min-width: 300px;
    margin-bottom: 30px;
    transition: all .4s;
}

.btn:hover {
    font-size: 100%;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}