
.rate-options-item {
    width: 50%;
    border: 1px solid #DFE5EC;
    line-height: 12px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
    font-family: "Open Sans", sans-serif;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer;
    transition: .4s;
}

.blue-background {
    background-color: #2196F3;
    transition: .4s;
}