.ticket-item {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    margin: 15px 0;
    border: 1px solid #DFE5EC;
    border-radius: 4px;
}

.top-container {
    display: flex;
    justify-content: space-between;
    margin: 20px;
    color: #2196F3;
}

.price {
    color: #2196F3;
    font-family: "Open Sans", sans-serif;
    font-size: 24px;
    font-weight: 600;
}

.way-line {

    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    font-size: 14px;
    margin-bottom: 10px;
    margin-left: 10px;
}

.left-part {
    display: flex;
    flex-direction: column;
    width: 33.333%;
}

.middle-part {
    display: flex;
    flex-direction: column;
    width: 33.333%;
}

.grey12 {
    font-size: 12px;
    color: #A0B0B9;
}

.flight-duration {
    text-transform: none;
}

.right-part {
    width: 33.333%;
}